import { Database } from '../../database/database';
import { createNodeDbHelper, NodeDbHelper } from '../../nodes/node-db-helper';
import { isFolderNode, SerializedFolderNode, SerializedNode } from '../../nodes/node-interface';

export abstract class AbstractNodeService {
  protected readonly dbHelper: NodeDbHelper;

  protected constructor(dbHelper?: NodeDbHelper) {
    this.dbHelper = dbHelper ?? createNodeDbHelper();
  }

  protected getNodesInContainer(dbHelper: NodeDbHelper, database: Database,
                                nodes: SerializedNode[]): Promise<SerializedNode[]> {
    const list = nodes.filter(n => isFolderNode(n))
                      .map(n => <SerializedFolderNode>n)
                      .map(n => dbHelper.listByContainer(database, n.sub_container.id)
                                        .then(nodes => this.getNodesInContainer(dbHelper, database, nodes)));
    return Promise.all([Promise.resolve(nodes), ...list]).then(r => r.flatMap(nodes => nodes));
  }
}
